.header {
    padding: 15px;
    grid-column: 12 span;
    background-color: firebrick;
    display: flex;
    align-items: center;

}

.header img {
    width: 50px;
}

.headerTitle {
    color: white;
    margin-left: 15px;
}

.loginBlock {
    margin-left: auto;
}

.loginBlock a {
    color: white;
    text-decoration: none;
}
.login {
    color: #fff;
    font-weight: bold;
}
.logoutBtn {
    cursor: pointer;
    background-color: #000;
    color: white;
    outline: none;
    border: none;
    padding: 5px;
}