.navBar {
  grid-column: 2 span;
}

.nav {
  background-color: firebrick;
  max-width: 160px;
  width: 100%;
  border-radius: 15px;
  height: 700px;
  position: fixed;
  z-index: 1000;
  padding: 15px;

}

.item {
  font-size: 20px;
  text-decoration: none;
  color: #fff;
  transition: all 0.3s;
}
.active {
  text-decoration: none;
  color: gold;
  font-weight: bold;
}

.sidebar {
  margin-top: 20px;
}

.sidebarContent {
  display: flex;

}

.sidebarTitle {
  font-size: 24px;
  color: #fff;
  text-align: center;
  margin-bottom: 15px;
}

.sidebarItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px;
}

.sidebarItem img {
  width: 20px;
  height: 20px;
  border-radius: 30px;
}

.sidebarName {
  font-size: 14px;
  color: white;
}